<template>
<div class="jobAssignment">
	<search :data="searchData" :config="searchConfig" @search="handlerSearch"></search>
	<my-table2 
        :data="tableData" 
        :config="tableConfig"
        @pageChange="pageChange"
        @see = "see"></my-table2>
 <my-toast :config="toastConfig" @submit="toastConfig.dialogVisible=false;$refs.ruleForm.resetFields()">
        <div class="form_box">
            <el-form :model="detailData" :rules="rules" ref="ruleForm" label-width="100px" style="margin-right:30px;">
                <el-row>
                  <el-col :span="8">
                        <el-form-item label="订单编号">
                            <span class="f_value">{{detailData.insideOrder}}</span>
                        </el-form-item>
                  </el-col>
                  <el-col :span="8">
                      <el-form-item label="产品信息">
                             <span class="f_value">{{detailData.prodMess}}</span>
                        </el-form-item>
                  </el-col>
                  <el-col :span="8">
                        <el-form-item label="工艺名称">
                             <span class="f_value">{{detailData.crafworkName}}</span>
                        </el-form-item>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="8">
                      <el-form-item label="工艺执行人">
                             <span class="f_value">{{detailData.doEmpName}}</span>
                        </el-form-item>
                  </el-col>
                  <el-col :span="8">
                        <el-form-item label="上传人">
                             <span class="f_value">{{detailData.sendEmp}}</span>
                        </el-form-item>
                  </el-col>
                  <el-col :span="8">
                      <el-form-item label="完工日期">
                             <span class="f_value">{{detailData.okDatetime}}</span>
                        </el-form-item>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="8">
                        <el-form-item label="确认状态">
                             <span class="f_value">{{detailData.status}}</span>
                        </el-form-item>
                  </el-col>
                  <el-col :span="8">
                  <!-- //waiting -->
                        <el-form-item label="上传时间">
                             <span class="f_value">{{detailData.sendDate}}</span>
                        </el-form-item>
                  </el-col>
                  <el-col :span="8">
                        <el-form-item label="说明">
                             <span class="f_value">{{detailData.desc}}</span>
                        </el-form-item>
                  </el-col>
                </el-row>
                <el-row v-if="detailData2">
                    <div class="img_box">
                        <li class="qw_pic_box" v-for="(item, i) in detailData2.pictureUrls">
                            <img class="qw_pic" :src="item? item: emptySrc">
                            <p class="img_title" >图片{{i+1}}</p>
                        </li>
                        <!-- <li class="qw_pic_box">
                            <img class="qw_pic" :src="emptySrc">
                            <p class="img_title">图片名称</p>
                        </li>
                        <li class="qw_pic_box">
                            <img class="qw_pic" :src="emptySrc">
                            <p class="img_title">图片名称</p>
                        </li> -->
                    </div>
                </el-row>
               <!--   -->
            </el-form>
            </div>
        </my-toast> 
</div>	
</template>
<script>
import myTable2 from "../../components/common/Table2";
import { checkRecord, findByMes, pictMessDetail } from "./api.js";
export default {
  components: {
    myTable2
  },
  data() {
    return {
      searchData: {},
      searchConfig: [
        [
          {
            label: "内部订单号",
            field: "insideOrder",
            filterable: true,
            type: "select",
            placeholder: "请输入/选择内部订单号",
            childrens: []
          },
          {
            label: "产品信息",
            field: "prodMess",
            type: "input",
            placeholder: "请输入手机号码"
          },
          {
            label: "确认状态",
            field: "isCheck",
            type: "select",
            placeholder: "请选择确认状态",
            childrens: [
              { name: "已完工", value: true },
              { name: "制作中", value: false }
            ]
          }
        ]
      ],
      tableData: [
        // {insideOrder:"ZD000000001",prodMess:"棉麻|PRO000002",crafworkName:"整经01",doEmpName:"马云",okDatetime:"2018-07-25",actHours:"10",sendEmp:"张三",sendDate:"2018-07-08",status:'进行中',desc:"描述详情描述详情"}
      ],
      tableConfig: {
        isHigh: false,
        isSelection: true,
        align: "center",
        loadShow: false,
        isPage: true,
        pageNo: 1,
        pageSize: 20,
        total: 0,
        colConfig: [
          {
            field: "insideOrder",
            isResize: true,
            label: "内部订单号",
            align: "center",
            type: "text"
          },
          {
            field: "prodMess",
            isResize: true,
            label: "产品信息",
            align: "center",
            type: "text"
          },
          {
            field: "crafworkName",
            isResize: true,
            label: "工艺名称",
            align: "center",
            type: "text"
          },
          {
            field: "doEmpName",
            isResize: true,
            label: "工艺执行人",
            align: "center",
            type: "text"
          },
          {
            field: "okDatetime",
            isResize: true,
            label: "完工日期",
            align: "center",
            type: "text"
          },
          {
            field: "getTime",
            isResize: true,
            label: "领派工时间",
            align: "center",
            type: "text"
          },
          {
            field: "actHours",
            isResize: true,
            label: "实际工时",
            align: "center",
            type: "text"
          },
          {
            field: "status",
            isResize: true,
            label: "确认状态",
            align: "center",
            type: "qw_status"
          },
          {
            width: "200px",
            isResize: true,
            label: "操作",
            align: "center",
            type: "button",
            btnData: [
              {
                name: "查看详情",
                method: "see"
              }
            ]
          }
        ]
      },
      toastConfig: {
        title: "查看详情", //弹窗标题
        dialogVisible: false, //弹窗显示
        width: "900px", //弹窗宽
        btnData: [
          {
            disabled: false,
            isShow: true,
            type: "primary",
            name: "关闭页面", //按钮名
            method: "submit", //回调函数
            bgColor: "dialog-bule" //按钮背景
          }
        ]
      },
      rules: {},
      detailData: {},
      emptySrc:
        "data:image/gif;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAADUlEQVQImWNgYGBgAAAABQABh6FO1AAAAABJRU5ErkJggg==",
      detailData2: { pictureUrls: [] }
    };
  },
  created() {
    this.getDropOrderMes();
    this.getList();
  },
  methods: {
    setAttr(data) {
      for (let attr in data) {
        if (
          undefined === data[attr] ||
          null === data[attr] ||
          "" === data[attr]
        ) {
          delete data[attr];
        }
      }
      return data;
    },
    getList() {
      console.log(this.searchData);
      let params = {
        pageSize: this.tableConfig.pageSize,
        pageNo: this.tableConfig.pageNo,
        ...this.searchData
      };
      console.log(params);
      params = this.setAttr(params);
      console.log(params);
      checkRecord({
        params: params,
        success: res => {
          if (res.items) {
            this.tableData = res.items;
            this.tableConfig.loadShow = false;
            this.tableConfig.total = res.numRows;
          }
        },
        Vue: this
      });
    },
    pageChange() {
      this.getList();
    },
    sizeChange() {
      this.getList();
    },
    see(data) {
      let that = this;
      that.detailData = data;
      let params = {
        insideOrder: data.insideOrder,
        prodNo: data.prodNo,
        crafworkId: data.crafworkId,
        getTime: data.getTime
      };
      pictMessDetail({
        params: params,
        success: res => {
          setTimeout(() => {
            that.detailData2 = res;

            that.detailData2.pictureUrlsStyle = [];
            let tem = [];
            for (let item of res.pictureUrls) {
              tem.push('background: url("' + item + '") 100px 100px;');
            }
            that.detailData2.pictureUrlsStyle = tem;
            console.log("that.detailData2==>", that.detailData2);
          }, 10);
          that.toastConfig.dialogVisible = true;
        },
        Vue: that
      });
    },
    //执行搜索
    handlerSearch(data) {
      this.tableConfig.pageNo = 1;
      this.getList();
    },
    //using获取订单信息下拉
    getDropOrderMes() {
      findByMes({
        params: { pageSize: 10000, pageNo: 1 },
        success: res => {
          console.log("res==>", res);
          let tem = [];
          for (let o in res) {
            tem.push({ name: res[o].insideOrder, value: res[o].insideOrder });
          }

          this.searchConfig[0][0].childrens = tem;
        },
        Vue: this
      });
    }
  }
};
</script>
<style>
.img_box {
  margin: 0px 35px;
}
.qw_pic_box {
  width: 100px;
  float: left;
}
.qw_pic {
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
  width: 100px;
  height: 100px;
}
.img_title {
  width: 100px;
  text-align: center;
}
</style>


