// export default routers
import Vue from 'vue'
import Router from 'vue-router'
//主路由
import index from '../page/index.vue'
import technology from '../page/technology/index.vue' 
import product from '../page/product/index'
import semiManfactures from '../page/semiManufactures/index'
import productProcess from '../page/productProcess/index'
import internalOrder from '../page/internalOrder/index'
import tasking from '../page/tasking/index'
import receive from '../page/receive/index'
import jobAssignment from '../page/jobAssignment/index'
import approve from '../page/approve/index.vue'
import customerReview from '../page/review/index'
import customerProcessReview from '../page/processReview/index'
import customerUploadData from '../page/customerUploadData/index'
import taskingRecord from '../page/taskingRecord/index'
import jobAssignmentHistory from '../page/jobAssignmentHistory/index'
import electronicHairMap from '../page/electronicHairMap/index.vue'
import seeUpload from '../page/checkCustomerData/index.vue' 
import controlCenterTree from '../page/controlCenterTree/index'
import manageTree from '../page/manageTree/index'
import jobAssignmentTree from '../page/jobAssignmentTree/index'
import electronicHairMapTree from '../page/electronicHairMapTree/index'
import customerManagementTree from '../page/customerManagementTree/index'
import setType from '../page/product/setType'
import productLibrary from '../page/product/productLibrary'
Vue.use(Router)
export default new Router({
  routes: [{
      path: "/index",
      name: 'index',
      component: index,
      children: [
        {
          path:'/jobAssignmentHistory',
          name: 'jobAssignmentHistory',
          component:jobAssignmentHistory
        },
        {
          path:'/taskingRecord',
          name: 'taskingRecord',
          component:taskingRecord
        },
        {
          path:'/customerUploadData',
          name: 'customerUploadData',
          component:customerUploadData
        },
        {
          path:'/customerProcessReview',
          name: 'customerProcessReview',
          component:customerProcessReview
        },
        {
          path:'/customerReview',
          name: 'customerReview',
          component:customerReview
        },
        {
          path:'/approve',
          name: 'approve',
          component:approve
        },
        {
          path:'/receive',
          name: 'receive',
          component:receive
        },
        {
          path:'/jobAssignment',
          name: 'jobAssignment',
          component:jobAssignment
        },
        {
          path:'/tasking',
          name: 'tasking',
          component:tasking
        },
        {
          path:'/controlCenterTree',
          name:'controlCenterTree',
          component:controlCenterTree
        },
        {
          path:'/manageTree',
          name:'manageTree',
          component:manageTree
        },
        {
          path:'/technology',
          name: 'technology',
          component:technology
        },
        {
          path:'/product',
          name: 'product',
          component:product
        },
        {
          path:'/semi-manufactures',
          name: 'semiManfactures',
          component:semiManfactures
        },
        {
          path:'/productProcess',
          name: 'productProcess',
          component:productProcess
        },
        {
          path:'/internalOrder',
          name: 'internalOrder',
          component:internalOrder
        },
        {
          path:'/electronicHairMap',
          name: 'electronicHairMap',
          component:electronicHairMap
        },{
          path:'/seeUpload',
          name:'seeUpload',
          component:seeUpload
        },{
          path:'/jobAssignmentTree',
          name:'jobAssignmentTree',
          component:jobAssignmentTree
        },{
          path:'/electronicHairMapTree',
          name:'electronicHairMapTree',
          component:electronicHairMapTree
        },{
          path:'/customerManagementTree',
          name:'customerManagementTree',
          component:customerManagementTree
        },{
          path:'/setType',
          name:'setType',
          component:setType
        },{
          path:'/productLibrary',
          name:'productLibrary',
          component:productLibrary
        }
      ]
    }
  ]
})



